.form-item{
  width: 100%;
  margin-bottom: 20px;
}

.input{
  height: 50px;
  display: flex;
  align-items: center;
  border: 1px solid #9C9C9C;
  transition: all .25s linear;
  font-family: inherit;
  border-radius: 8px;
  padding: 0 15px;
  font-size: 14px;
  font-weight: 500;
  background: var(--white);
  width: 100%;
  color: var(--black);
  &:hover{
    border: 1px solid #8a8787;
  }
  &::placeholder{
    transition: all .25s linear;
    color: #9C9C9C;
  }
  &:focus{
    &::placeholder{
      opacity: 0;
    }
  }
  &.error{
    border: 1px solid var(--red);
    &::placeholder{
      color: var(--red);
    }
  }
}

.textarea{
  resize: none;
  height: 200px;
  padding: 15px;
}

.input-absolute{
  position: absolute;
  opacity: 0;
  z-index: 1;
  cursor: pointer;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.agreed{
  display: flex;
  align-items: center;
}

.agreed-icon{
  position: relative;
  flex-shrink: 0;
  margin-right: 10px;
  .input-absolute{
    &.error{
      & + .agreed-icon-check{
        border: 1px solid var(--red);
      }
    }
    &:checked{
      & + .agreed-icon-check{
        .icon{
          opacity: 1;
        }
      }
    }
  }
}

.agreed-icon-check{
  border: 1px solid var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  border-radius: 5px;
  height: 24px;
  .icon{
    width: 12px;
    height: 12px;
    fill: var(--white);
    opacity: 0;
  }
}

.agreed-text{
  font-size: 14px;
  font-weight: 600;
  a{
    text-decoration: underline;
  }
}