/* Основные стили */
body {
  font-family: 'Inter', sans-serif;
  font-size: 18px;
  line-height: 1.4;
  color: var(--black);
  background-color: var(--white);
  @media(max-width: 991px) {
    font-size: 16px;
  }
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  font-weight: inherit;
}

/* Ссылки */
a {
  text-decoration: none;
  color: inherit;
  transition: all .25s linear;
}

/* Выделение текста */
strong {
  font-weight: bold;
}

em {
  font-style: italic;
}

picture{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}