/* Стили для контейнеров и сеток */
.container {
  width: 100%;
  margin: 0 auto;
  max-width: 1352px;
  padding: 0 26px;
}

/* Выравнивание элементов */
.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

address{
  font-style: normal;
}

section{
  padding: 80px 0;
  @media(max-width: 991px) {
    padding: 40px 0;
  }
}

.l-h-1-2{
  line-height: 1.2;
}