.header{
  margin-bottom: 32px;
  z-index: 6;
  background: var(--white);
  position: relative;
}

.header-logo{
  @media(max-width: 1279px) {
    width: 60px;
  }
}

.header-top{
  padding: 19px 0;
  @media(max-width: 1279px) {
    padding: 10px 0 19px;
    background: var(--blue);
    .social{
      display: none;
    }
  }
}

.header-event-phone-mobile{
  display: none;
  @media(max-width: 1279px) {
    background: var(--white);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 40px;
    margin-right: 20px;
    height: 40px;
  }
  @media(max-width: 767px) {
    margin-right: 10px;
  }
}

.hamburger{
  display: none;
  @media(max-width: 1279px) {
    margin-left: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 40px;
    height: 40px;
    background: var(--white);
    border-radius: 5px;
  }
  @media(max-width: 767px) {
    margin-left: 10px;
  }
}

.line{
  width: 24px;
  height: 2px;
  background: var(--blue);
  margin: 2px 0;
  border-radius: 4px;
}

.header-address{
  gap: 32px;
  @media(max-width: 1279px) {
    display: none;
  }
}

.social{
  gap: 8px;
}

.social-link{
  width: 32px;
  height: 32px;
}

.header-event{
  gap: 20px;
  @media(max-width: 1279px) {
    gap: 4px;
  }
}

.header-event__button{
  border: 1px solid var(--blue);
}

.header-event__button-icon{
  width: 40px;
  height: 40px;
}

.header-event__button-text{
  padding: 0 16px 0 8px;
  @media(max-width: 1279px) {
    display: none;
  }
}

.button{
  height: 50px;
  border-radius: 10px;
  padding: 0 30px;
  font-family: inherit;
  font-size: inherit;
  &.h-72{
    height: 72px;
    @media(max-width: 991px) {
      height: 60px;
    }
  }
  &.h-60{
    height: 60px;
    @media(max-width: 991px) {
      height: 52px;
    }
  }
}

.header-flex{
  padding: 16px 32px;
}

.header-menu__items{
  gap: 29px;
  @media(max-width: 1350px) {
    gap: 20px;
  }
}

.header-menu__item{
  font-size: 16px;
  font-weight: 500;
}

.header-menu__item_dropdown{
  gap: 4px;
}

.section-start{
  padding-top: 0;
  @media(max-width: 991px) {
    padding-bottom: 0;
  }
}

.start-slide{
  height: 600px;
  background-size: cover;
  padding: 50px;
  display: block;
  @media(max-width: 991px) {
    padding: 40px 26px;
    height: 520px;
  }
}

.start-slide__content{
  color: var(--white);
  position: relative;
  max-width: 721px;
  @media(max-width: 991px) {
    p{
      font-size: 16px;
    }
  }
}

.mw-900{
  max-width: 900px;
}

.header-main{
  @media(max-width: 1279px) {
    display: none;
  }
}

.close-mobile-menu, .menu-more, .social_mobile, .contact-mobile{
  @media(min-width: 1279px) {
    display: none !important;
  }
}

.start-container{
  position: relative;
  @media(max-width: 991px) {
    margin: -32px -26px 0;
    background: var(--blue);
  }
}

.filter-cruise{
  background: var(--white);
  position: absolute;
  bottom: 60px;
  left: 60px;
  width: calc(100% - 120px);
  padding: 32px;
  border-radius: 20px;
  z-index: 1;
  @media(max-width: 1279px) {
    bottom: 10px;
  }
  @media(max-width: 991px) {
    margin: -190px 0 0;
    padding: 32px 26px;
    position: relative;
    bottom: auto;
    left: auto;
    width: auto;
    border-radius: 20px 20px 0 0;
  }
}

.filter-cruise__head{
  display: flex;
  gap: 32px;
  align-items: center;
  margin-bottom: 24px;
  @media(max-width: 1279px) {
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
  }
  @media(max-width: 991px) {
    gap: 16px;
    margin-bottom: 16px;
  }
}

.filter-cruise__radio{
  display: flex;
  gap: 12px;
  @media(max-width: 991px) {
    flex-flow: row wrap;
    gap: 8px;
  }
}

.filter-radio{
  position: relative;
  input{
    &:checked{
      & + .filter-radio__cruise{
        background: var(--blue);
        color: var(--white);
      }
    }
  }
  &:hover{
   .filter-radio__cruise{
     background: var(--gray-200);
   }
  }
}

.filter-radio__cruise{
  width: 245px;
  height: 40px;
  transition: all .25s linear;
  background: #EDEDED;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  @media(max-width: 991px) {
    width: auto;
    height: 38px;
    padding: 5px 15px;
  }
  span{
    margin-right: 4px;
  }
}

.filter-cruise__form{
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 16px;
  @media(max-width: 1279px) {
    grid-template-columns: repeat(3, 1fr);
  }
  @media(max-width: 991px) {
    display: flex;
    flex-direction: column;
  }
}

.filter-cruise__default{
  width: 100%;
  display: flex;
  align-items: center;
  border: 1px solid var(--gray-200);
  border-radius: 8px;
  height: 52px;
  padding: 10px;
  transition: all .25s linear;
  cursor: pointer;
  &:hover{
    border: 1px solid var(--blue);
  }
  @media(max-width: 991px) {

  }
}

.filter-cruise__item{
  width: 100%;
  overflow: hidden;
  font-size: 14px;
}

.filter-cruise__default-icon{
  flex-shrink: 0;
  margin-right: 4px;
  display: flex;
  align-items: center;
  svg{
    fill: #9C9C9C;
  }
}

.filter-cruise__default-arrow{
  flex-shrink: 0;
  margin-left: 4px;
  display: flex;
  align-items: center;
  svg{
    fill: transparent;
    stroke: #9C9C9C;
  }
}

.filter-cruise__default-text{
  color: #9C9C9C;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: calc(100% - 52px);
}

.dop-menu{
  display: none;
}

.blackout{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 4;
  background: rgba(0, 0, 0, 0.7);
  transition: all .25s linear;
  pointer-events: none;
  opacity: 0;
}

.header-menu__item_mobile{
  display: none;
  @media(max-width: 1279px) {
    display: flex;
  }
}